import React, { useContext } from 'react'
import { Button, Col, Container, Row } from 'react-bootstrap'
import { Link } from 'react-router-dom';
import CartContext from '../Store/cart-context'
import classes from "./Product.module.css";

export default function Product(props) {
    const cartCtx = useContext(CartContext)

    const addInCart_handler = (data) => {
        const product_detail = {
            title: data.title,
            imageUrl: data.imageURL,
            price: data.price,
            identity: data.id,
            database: data.database_id,
            quantity: 1
        }

        cartCtx.addItem(product_detail)
    }

    const fetching = cartCtx.fetching;

    return (
        <Col style={{ width: "45%" }} className={`${classes.whole_product} p-3`}>
            <Container >
                <h3 className='text-center mb-3 fs-4'>{props.title}</h3>
                <Link to={`/store/${props.id}`}>
                    <img src={props.imageURL} alt="Product view" style={{ width: "100%" }} />
                </Link>
                <Row className='my-3 align-items-center'>
                    <Col>Rs. <span>{props.price}</span></Col>
                    <Col>
                        {!fetching && <Button variant='info' onClick={() => addInCart_handler(props)} style={{ float: "right" }}>Add To Cart</Button>}
                    </Col>
                </Row>
                <p hidden>{props.database_id}</p>
                <p hidden>{props.id}</p>
            </Container>
        </Col >
    )
}
